            </v-list-item>
            <!-- FIXME: no name data -->
            <v-list-item two-line>
                <v-list-item-content>
                    <v-list-item-title>First name</v-list-item-title>
                    <v-list-item-subtitle>xxxx</v-list-item-subtitle>
